.mylessons_deeplinker {
    width: 200px;
    position: absolute;
    left: 5px;
    height: 45px;
    bottom: 20px;
    left: 25px;
    border-radius: 25px;
    font-size: 25px;
    /* vertical align the button to the middle*/
    background-color:rgb(255, 52, 16);
    transition: 0.3s;
    cursor: pointer;
}
.mylessons_deeplinker:hover {
    background-color:rgb(251, 94, 89);
    transition: 0.3s;
}

